.edit-button-SMTP{
    border: none;
    background-color: #fff;
    padding: 4px 7px;
    margin-right: 5px;
    &:hover {
        transform: scale(1.2);
    }
}
.updateSMTP-success{
    font-size: 16px;
    color: rgb(19, 158, 19);
    font-weight: 700;
}

.updateSMTP-container{
   position: absolute;
   display: flex;
   justify-content: flex-end;
   width: 100%;
   padding-right: 4px;
}